import {
  GET_SECTIONB_FAIL,
  GET_SECTIONB_REQUEST,
  GET_SECTIONB_SUCCESS,
} from "../constants/initConstants";

export const sectionBReducer = (state = { sectionB: [] }, action) => {
  switch (action.type) {
    case GET_SECTIONB_REQUEST:
      return { ...state, loading: true };
    case GET_SECTIONB_SUCCESS:
      return { loading: false, success: true, sectionB: action.payload.data };
    case GET_SECTIONB_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
