import React from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { useSelector } from "react-redux";
import { GiPositionMarker } from "react-icons/gi";
import { Link, Route } from "react-router-dom";
import { Button } from "@chakra-ui/button";

const Profile = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Profile" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.profileGrid}>
            <div className={styles.contentSm}>
              <div className={styles.profilePix}>
                <img
                  src={`/uploads/${user ? user.photo : ""}`}
                  alt={user && user.firstname}
                />
              </div>
            </div>
            <div className={`${styles.contentSm} ${styles.profile}`}>
              <h3>
                {user &&
                  user.firstname + " " + user.middlename + " " + user.lastname}
                &nbsp;&nbsp;
                <Link to="/edit/profile" className="btn btn-info">
                  <Button colorScheme="teal" size="sm">
                    Edit Profile
                  </Button>
                </Link>
              </h3>

              <p style={{ color: "teal" }}>
                <GiPositionMarker style={{ float: "left" }} />{" "}
                <b>{user ? user.location : ""} Nigeria</b>
              </p>
              <small>
                <b>Role:</b> {user ? user.position : ""}
              </small>
              <br />
              <small>
                <b>Department:</b> {user ? user.department : ""}
              </small>
              <br />
              <small>
                <b className="text-info">Manager:</b> {user ? user.manager : ""}
              </small>
              <p>Brief Description</p>
              <small>{user ? user.description : ""}</small>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
