import React, { useEffect, useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Link, Route } from "react-router-dom";
import {
  Button,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  FormControl,
  FormLabel,
  Select,
  Center,
  CircularProgress,
  AlertIcon,
  Alert,
  useToast,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import {
  commenceAppraisal,
  currentAppraisal,
  AllAppraisal,
  updateAppraisal,
} from "../redux/actions/appraisalActions";
import {
  START_APPRAISAL_RESET,
  UPDATE_APPRAISAL_RESET,
} from "../redux/constants/appraisalConstants";

const StartAppraisal = () => {
  const toast = useToast();
  const [quarter, setQuarter] = useState("");
  const dispatch = useDispatch();
  const session = new Date().getFullYear();

  //   Fetch All Appraisal
  const all = useSelector((state) => state.all);
  const { appraisal } = all;

  //   Create&&Start Apraisal
  const start = useSelector((state) => state.start);
  const { loading, error, success } = start;
  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(commenceAppraisal(session, quarter));
  };
  if (success) {
    toast({
      title: "Success",
      description: "Appraisal Started Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: START_APPRAISAL_RESET });
    dispatch(AllAppraisal());
    dispatch(currentAppraisal());
  }

  //   Update  Appraisal
  const update = useSelector((state) => state.update);
  const { success: sucessUpdate, error: errorUpdate } = update;
  const updateHandler = (id, status) => {
    dispatch(updateAppraisal(id, status));
  };
  if (sucessUpdate) {
    dispatch({ type: UPDATE_APPRAISAL_RESET });
    dispatch(AllAppraisal());
    dispatch(currentAppraisal());
  }

  useEffect(() => {
    dispatch(AllAppraisal());
  }, [dispatch]);

  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Appraisal" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            <h3>Start Appraisal</h3>
            {error && (
              <Alert>
                <AlertIcon />
                {error}
              </Alert>
            )}

            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
                Fetching Section A
              </Center>
            ) : (
              <form onSubmit={submitHandler}>
                <div className={styles.formGrid}>
                  <FormControl id="quarter" py="2">
                    <FormLabel>Quarter</FormLabel>
                    <Select
                      placeholder="Select Quarter"
                      value={quarter}
                      onChange={(e) => setQuarter(e.target.value)}
                    >
                      <option value="First Quarter">First Quarter</option>
                      <option value="Second Quarter">Second Quarter</option>
                      <option value="Third Quarter">Third Quarter</option>
                      <option value="Fourth Quarter">Fourth Quarter</option>
                    </Select>
                  </FormControl>
                  <FormControl id="quarter" py="2">
                    <FormLabel style={{ visibility: "hidden" }}>
                      Start
                    </FormLabel>
                    <Button type="submit" colorScheme="teal" isFullWidth>
                      Start
                    </Button>
                  </FormControl>
                </div>
              </form>
            )}
            {errorUpdate && (
              <Alert status="error">
                <AlertIcon />
                {errorUpdate}
              </Alert>
            )}
            <Table variant="striped" colorScheme="teal">
              <TableCaption>Lban Appraisal</TableCaption>
              <Thead>
                <Tr>
                  <Th>Year</Th>
                  <Th>Quarter</Th>
                  <Th>Status</Th>
                  <Th>Actions</Th>
                </Tr>
              </Thead>
              <Tbody>
                {appraisal &&
                  appraisal.map((item) => (
                    <Tr key={item._id}>
                      <Td>{item.session}</Td>
                      <Td>{item.quarter}</Td>
                      <Td>{item.status}</Td>
                      <Td>
                        <Button
                          type="button"
                          colorScheme={
                            item.status === "Started" ? "red" : "teal"
                          }
                          onClick={() =>
                            updateHandler(
                              item._id,
                              item.status === "Started" ? "Stopped" : "Started"
                            )
                          }
                        >
                          {item.status === "Started" ? "Stop" : "Start"}
                        </Button>
                      </Td>
                    </Tr>
                  ))}
              </Tbody>
            </Table>

            <Link to="/appraisal/sectionA">
              <Button colorScheme="teal">Start Appraisal</Button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default StartAppraisal;
