import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from 'react-router-dom'
import styles from './nav.module.css'
import logo from "../../assets/purple.png";
import { logout } from "../../redux/actions/userActions";
import { FaUserCircle, FaTachometerAlt, FaUsers, FaRegChartBar, FaPowerOff } from "react-icons/fa";
import { GiAbstract050 } from "react-icons/gi";
import { Center } from "@chakra-ui/react";


const Navigation = ({ history, dashboard, profile, appraisal, team, staff }) => {
    const dispatch = useDispatch();

    const [mobile, setMobile] = useState(false)
    const [open, setOpen] = useState(false)


    const userDetails = useSelector((state) => state.userDetails);
    const { user } = userDetails;

    const logouthandler = () => {
        dispatch(logout());
        history.push("/")
    }

    const expand = () => {
        setMobile(true)
    }
    const collapse = () => {
        setMobile(false)
    }

    const toggle = () => {
        setOpen(true)
    }
    if (!user) {
        history.push("/")
    }
    return (
        <>
            <div className={styles.icon}>
                <button className="btn btn-dark" onClick={expand}><GiAbstract050 style={{ float: "left" }} />&nbsp;&nbsp;Open Navigation</button>
            </div>

            <div className={mobile ? styles.mobilenavigation : styles.navigation}>
                <div className={styles.closeicon}>
                    <button className="btn btn-dark" onClick={collapse}> Close Navigation</button>
                </div>
                <div className={`${styles.logo} text-center`}>
                    <Center>
                        <img src={logo} alt="Lotus Beta Analytics" />
                    </Center>
                    <small>Lotus Beta Analytics</small>
                </div>
                <div className={styles.profile}>
                    <div className={styles.photo}>
                        <img src={`/uploads/${user ? user.photo : ""}`} alt={user && user.firstname} />
                    </div>
                    <small>{user && user.firstname}</small><br />

                </div>
                <div className={styles.url}>
                    <ul>
                        <li className={dashboard}><Link to="/dashboard"><FaTachometerAlt />Dashboard</Link></li>
                        <li className={profile}><Link to="/profile"><FaUserCircle />Profile</Link></li>
                        <li className={appraisal} onClick={toggle}><Link to="#"><FaRegChartBar />Appraisal </Link>
                        </li>
                        <div className={`${styles.urlActive} ${open && styles.open}`}>
                            <ul>
                                <Link to="/appraisal/start"><li>Open Appraisal</li></Link>
                                <Link to="/appraisal"><li>Start Appraisal</li></Link>
                                <Link to="/appraisal/view"><li>View Initiatives</li></Link>
                                <Link to="/appraisal/set"><li>Set Initiatives</li></Link>
                            </ul>
                        </div>


                        <li className={team}><Link to="/myteam"><FaUsers />My Team</Link></li>
                        {user && user.role === "Admin" ? <li className={staff}><Link to="/users"><FaUsers />All Staffs</Link></li> : ""}
                        {user && user.role === "HR" ? <li className={staff}><Link to="/attendance"><FaUsers />Attendance</Link></li> : ""}
                        <li><Link to="/#logout" onClick={logouthandler}><FaPowerOff />Logout</Link></li>
                    </ul>
                </div>

            </div>
        </>
    )
}

export default Navigation
