import {
  USER_LOGIN_REQUEST,
  USER_LOGIN_SUCCESS,
  USER_LOGIN_FAIL,
  USER_LOGOUT,
  USER_DETAILS_REQUEST,
  USER_DETAILS_SUCCESS,
  USER_DETAILS_FAIL,
  USER_DETAILS_RESET,
  USER_LOGIN_RESET,
  USER_GET_DEPT_FAIL,
  USER_GET_DEPT_REQUEST,
  USER_GET_DEPT_SUCCESS,
  GET_MANAGER_FAIL,
  GET_MANAGER_REQUEST,
  GET_MANAGER_SUCCESS,
} from "../constants/userConstants";
export const userLoginReducer = (state = {}, action) => {
  switch (action.type) {
    case USER_LOGIN_REQUEST:
      return { loading: true };
    case USER_LOGIN_SUCCESS:
      return { loading: false, success: true, userInfo: action.payload };
    case USER_LOGIN_FAIL:
      return { loading: false, error: action.payload };
    case USER_LOGOUT:
      return {};
    case USER_LOGIN_RESET:
      return {};

    default:
      return state;
  }
};

export const userDetailsReducer = (state = { user: {} }, action) => {
  switch (action.type) {
    case USER_DETAILS_REQUEST:
      return { ...state, loading: true };
    case USER_DETAILS_SUCCESS:
      return { loading: false, user: action.payload.data };
    case USER_DETAILS_FAIL:
      return { loading: false, error: action.payload };
    case USER_DETAILS_RESET:
      return { user: {} };
    default:
      return state;
  }
};

export const getDeptReducer = (state = { lbandept: [] }, action) => {
  switch (action.type) {
    case USER_GET_DEPT_REQUEST:
      return { ...state, loading: true };
    case USER_GET_DEPT_SUCCESS:
      return {
        loading: false,
        success: true,
        lbandept: action.payload.data,
      };
    case USER_GET_DEPT_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const getManagerReducer = (state = { manager: [] }, action) => {
  switch (action.type) {
    case GET_MANAGER_REQUEST:
      return { ...state, loading: true };
    case GET_MANAGER_SUCCESS:
      return {
        loading: false,
        success: true,
        manager: action.payload.data,
      };
    case GET_MANAGER_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};
