import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import AccountInformation from "./screen/AccountInfo";
import Dashboard from "./screen/Dashboard";
import EditProfile from "./screen/EditProfile";
import EmergencyContact from "./screen/EmergencyContact";
import HomeScreen from "./screen/HomeScreen";
import OfficialInformation from "./screen/OfficialInfo";
import PhotoUpload from "./screen/PhotoUpload";
import Profile from "./screen/Profile";
import Appraisal from "./screen/Appraisal";
import SectionA from "./screen/SectionA";
import StartAppraisal from "./screen/StartAppraisal";
import SetInit from "./screen/SetInit";
import SectionB from "./screen/SectionB";
import Attendance from "./screen/Attendance";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={HomeScreen} />
        <Route path="/dashboard" exact component={Dashboard} />
        <Route path="/profile" exact component={Profile} />
        <Route path="/edit/profile" exact component={EditProfile} />
        <Route
          path="/edit/emergencyContact"
          exact
          component={EmergencyContact}
        />
        <Route
          path="/edit/officialInformation"
          exact
          component={OfficialInformation}
        />
        <Route
          path="/edit/accountInformation"
          exact
          component={AccountInformation}
        />
        <Route path="/edit/photo" exact component={PhotoUpload} />
        <Route path="/appraisal" exact component={Appraisal} />
        <Route path="/appraisal/start" exact component={StartAppraisal} />
        <Route path="/appraisal/set" exact component={SetInit} />
        <Route path="/appraisal/sectionA" exact component={SectionA} />
        <Route path="/appraisal/sectionB" exact component={SectionB} />
        <Route path="/attendance" exact component={Attendance} />
      </Switch>
    </Router>
  );
}

export default App;
