import React, { useEffect, useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  FormControl,
  FormLabel,
  Input,
  Select,
  Button,
  CircularProgress,
  useToast,
  Center,
} from "@chakra-ui/react";
import Steps from "../components/UI/Steps";
import { Link } from "react-router-dom";
import { updateOInfo } from "../redux/actions/profileActions";
import { USER_OINFO_RESET } from "../redux/constants/profileConstants";
import {
  getDept,
  getUserDetails,
  getMyManager,
} from "../redux/actions/userActions";
import { Route } from "react-router-dom";
const OfficialInformation = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  const [department, setDepartment] = useState(user && user.department);
  const [position, setPosition] = useState(user && user.position);
  const [resumptionDate, setResumptionDate] = useState(
    user && user.resumptionDate
  );
  const [location, setLocation] = useState(user && user.location);
  const [cug, setCUG] = useState(user && user.cug);
  const [manager, setManager] = useState(user && user.manager);

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateOInfo(department, position, resumptionDate, location, cug, manager)
    );
  };
  const departments = useSelector((state) => state.departments);
  const { lbandept } = departments;
  const officialInformation = useSelector((state) => state.officialInformation);
  const { loading, error, success } = officialInformation;
  const myManager = useSelector((state) => state.myManager);
  const { manager: man } = myManager;
  const toast = useToast();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_OINFO_RESET });
  }

  if (success) {
    toast({
      title: "Success",
      description: "Official Information Updated Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_OINFO_RESET });
    dispatch(getUserDetails());
  }
  useEffect(() => {
    dispatch(getDept());
    dispatch(getMyManager());
  }, [dispatch]);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Edit Profile" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.profileGrid}>
            <div className={styles.contentSm}>
              <div className={styles.profilePix}>
                <img
                  src={`/uploads/${user ? user.photo : ""}`}
                  alt={user && user.firstname}
                />
              </div>
            </div>
            <div className={`${styles.contentSm} ${styles.profile}`}>
              <Steps step3 />
              {loading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <form onSubmit={submitHandler}>
                  <div className={styles.formGrid}>
                    <FormControl id="resumptionDate" py="2">
                      <FormLabel>Resumption Date</FormLabel>
                      <Input
                        type="date"
                        value={resumptionDate}
                        onChange={(e) => setResumptionDate(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="position" py="2">
                      <FormLabel>Position</FormLabel>
                      <Input
                        type="text"
                        value={position}
                        onChange={(e) => setPosition(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="department" py="2">
                      <FormLabel>Department</FormLabel>
                      <Select
                        placeholder="Select Department"
                        value={department}
                        onChange={(e) => setDepartment(e.target.value)}
                      >
                        {lbandept &&
                          lbandept.map((items) => (
                            <option value={items.name} key={items._id}>
                              {items.name}
                            </option>
                          ))}
                      </Select>
                    </FormControl>
                    <FormControl id="cug" py="2">
                      <FormLabel>CUG Number</FormLabel>
                      <Input
                        type="tel"
                        value={cug}
                        onChange={(e) => setCUG(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="location" py="2">
                      <FormLabel>Location</FormLabel>
                      <Select
                        placeholder="Select Location"
                        value={location}
                        onChange={(e) => setLocation(e.target.value)}
                      >
                        <option value="Lagos">Lagos</option>
                        <option value="Abuja">Abuja</option>
                      </Select>
                    </FormControl>
                    <FormControl id="manager" py="2">
                      <FormLabel>Manager</FormLabel>
                      <Select
                        placeholder="Select Manager"
                        value={manager}
                        onChange={(e) => setManager(e.target.value)}
                      >
                        {man &&
                          man.map((items) => (
                            <option value={items._id} key={items._id}>
                              {items.firstname + " " + items.lastname}
                            </option>
                          ))}
                      </Select>
                    </FormControl>
                  </div>
                  <div className={styles.formGrid}>
                    <FormControl py="2">
                      <Button colorScheme="green" type="submit" isFullWidth>
                        Save Information
                      </Button>
                    </FormControl>
                    <FormControl py="2">
                      <Link to="/edit/accountInformation">
                        <Button colorScheme="blue" isFullWidth>
                          Account Information
                        </Button>
                      </Link>
                    </FormControl>
                  </div>
                </form>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OfficialInformation;
