import axios from "axios";
import {
  GET_SECTIONA_SUCCESS,
  GET_SECTIONA_REQUEST,
  GET_SECTIONA_FAIL,
  ANSWER_SECTIONA_SUCCESS,
  ANSWER_SECTIONA_REQUEST,
  ANSWER_SECTIONA_FAIL,
  PREVIOUS_SCORE_FAIL,
  PREVIOUS_SCORE_REQUEST,
  PREVIOUS_SCORE_SUCCESS,
  GET_CURRENTAPPRAISAL_FAIL,
  GET_CURRENTAPPRAISAL_REQUEST,
  GET_CURRENTAPPRAISAL_SUCCESS,
  START_APPRAISAL_FAIL,
  START_APPRAISAL_REQUEST,
  START_APPRAISAL_SUCCESS,
  GET_ALL_SUCCESS,
  GET_ALL_REQUEST,
  GET_ALL_FAIL,
  UPDATE_APPRAISAL_SUCCESS,
  UPDATE_APPRAISAL_REQUEST,
  UPDATE_APPRAISAL_FAIL,
  GET_INIT_SUCCESS,
  GET_INIT_REQUEST,
  GET_INIT_FAIL,
  ADD_INIT_REQUEST,
  ADD_INIT_FAIL,
  ADD_INIT_SUCCESS,
  GET_SCORE_SUCCESS,
  GET_SCORE_REQUEST,
  GET_SCORE_FAIL,
  UPLOAD_SCORE_FAIL,
  UPLOAD_SCORE_REQUEST,
  UPLOAD_SCORE_SUCCESS,
} from "../constants/appraisalConstants";

export const fetchSectionA = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_SECTIONA_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(
      `/api/v1/staff/appraisal/sectiona/`,
      config
    );

    dispatch({
      type: GET_SECTIONA_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_SECTIONA_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

