import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  FormControl,
  FormLabel,
  Input,
  Select,
  Textarea,
  Button,
  CircularProgress,
  useToast,
  Center,
} from "@chakra-ui/react";
import states from "../components/states";
import Steps from "../components/UI/Steps";
import { Link, Route } from "react-router-dom";
import { updateBasicInfo } from "../redux/actions/profileActions";
import { USER_BASIC_INFO_RESET } from "../redux/constants/profileConstants";
import { getUserDetails } from "../redux/actions/userActions";

const EditProfile = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  const [firstname, setFirstName] = useState(user && user.firstname);
  const [middlename, setMiddleName] = useState(user && user.middlename);
  const [lastname, setLastName] = useState(user && user.lastname);
  const [email, setEmail] = useState(user && user.email);
  const [dob, setDob] = useState(user && user.dob);
  const [gender, setGender] = useState(user && user.gender);
  const [state, setState] = useState(user && user.state);
  const [mobile, setMobile] = useState(user && user.mobile);
  const [address, setAddress] = useState(user && user.address);
  const [description, setDescription] = useState(user && user.description);

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateBasicInfo(
        firstname,
        middlename,
        lastname,
        email,
        dob,
        gender,
        state,
        mobile,
        address,
        description
      )
    );
  };
  const basicInformation = useSelector((state) => state.basicInformation);
  const { loading, error, success } = basicInformation;
  const toast = useToast();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_BASIC_INFO_RESET });
  }

  if (success) {
    toast({
      title: "Success",
      description: "Basic Information Updated Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_BASIC_INFO_RESET });
    dispatch(getUserDetails());
  }
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Edit Profile" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.profileGrid}>
            <div className={styles.contentSm}>
              <div className={styles.profilePix}>
                <img
                  src={`/uploads/${user ? user.photo : ""}`}
                  alt={user && user.firstname}
                />
              </div>
            </div>
            <div className={`${styles.contentSm} ${styles.profile}`}>
              <Steps step1 />
              {loading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <form onSubmit={submitHandler}>
                  <div className={styles.formGrid}>
                    <FormControl id="firstname" py="2">
                      <FormLabel>First Name</FormLabel>
                      <Input
                        type="text"
                        value={firstname}
                        onChange={(e) => setFirstName(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="middlename" py="2">
                      <FormLabel>Middle Name</FormLabel>
                      <Input
                        type="text"
                        value={middlename}
                        onChange={(e) => setMiddleName(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="lastname" py="2">
                      <FormLabel>Last Name</FormLabel>
                      <Input
                        type="text"
                        value={lastname}
                        onChange={(e) => setLastName(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="email" py="2">
                      <FormLabel>Email</FormLabel>
                      <Input
                        type="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        readOnly
                      />
                    </FormControl>
                    <FormControl id="dob" py="2">
                      <FormLabel>Date of Birth</FormLabel>
                      <Input
                        type="date"
                        value={dob}
                        onChange={(e) => setDob(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="gender" py="2">
                      <FormLabel>Gender</FormLabel>
                      <Select
                        placeholder="Select Gender"
                        value={gender}
                        onChange={(e) => setGender(e.target.value)}
                      >
                        <option value="Male">Male</option>
                        <option value="Female">Female</option>
                      </Select>
                    </FormControl>
                    <FormControl id="mobile" py="2">
                      <FormLabel>Mobile</FormLabel>
                      <Input
                        type="tel"
                        value={mobile}
                        onChange={(e) => setMobile(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="state" py="2">
                      <FormLabel>State of Origin</FormLabel>
                      <Select
                        placeholder="Select State"
                        value={state}
                        onChange={(e) => setState(e.target.value)}
                      >
                        {states.map((item) => (
                          <option value={item} key={item}>
                            {item}
                          </option>
                        ))}
                      </Select>
                    </FormControl>
                    <FormControl id="addrees" py="2">
                      <FormLabel>Address</FormLabel>
                      <Textarea
                        value={address}
                        onChange={(e) => setAddress(e.target.value)}
                      ></Textarea>
                    </FormControl>
                    <FormControl id="description" py="2">
                      <FormLabel>
                        Brief Description about {user && user.firstname}
                      </FormLabel>
                      <Textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                      ></Textarea>
                    </FormControl>
                    <FormControl py="2">
                      <Button colorScheme="green" type="submit" isFullWidth>
                        Save Information
                      </Button>
                    </FormControl>
                    <FormControl py="2">
                      <Link to="/edit/emergencyContact">
                        <Button colorScheme="blue" isFullWidth>
                          Emergency Contact
                        </Button>
                      </Link>
                    </FormControl>
                  </div>
                </form>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EditProfile;
