import React from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Link, Route } from "react-router-dom";
import { Button } from "@chakra-ui/button";

const Appraisal = () => {
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Appraisal" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            <h3>Balanced Scorecard</h3>
            <p>
              The business has adopted the of use a Balanced Scorecard, to
              provide a framework to manage resources to achieve the above
              strategic objectives. To grow as business we are going to be
              driven by better learning and innovation, which in turn leads to
              better internal processes, which then improves customer
              satisfaction, in turn improving the company’s financial
              performance.
            </p>
            <br />
            <p>
              There are four basic perspectives to take with the KPI balanced
              scorecard, and in the following hierarchical order:
            </p>
            <br />
            <ul>
              <li>
                <b>Financial perspective </b>– Establish KPIs to track the
                financial performance of the company, including individual
                departments, functions, and divisions.
              </li>
              <li>
                <b>Customer perspective </b>– KPIs to track customer
                satisfaction, attitudes, and market share goals.
              </li>
              <li>
                <b>Internal process perspective</b> – These KPIs provide metrics
                for the specific internal operational goals needed to meet
                customer objectives.
              </li>
              <li>
                <b>The learning and growth or innovation perspective</b>-
                Intangible drivers for future success such as human capital,
                organizational capital, training, informational systems, etc.
              </li>
            </ul>
            <Link to="/appraisal/sectionA">
              <Button colorScheme="teal">Start Appraisal</Button>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Appraisal;
