import axios from "axios";
import {
  GET_SECTIONB_SUCCESS,
  GET_SECTIONB_REQUEST,
  GET_SECTIONB_FAIL,
} from "../constants/initConstants";

export const fetchSectionB = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_SECTIONB_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(`/api/v1/staff/initiative/all`, config);

    dispatch({
      type: GET_SECTIONB_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_SECTIONB_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
