import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  FormControl,
  FormLabel,
  Input,
  Select,
  Button,
  CircularProgress,
  useToast,
  Center,
} from "@chakra-ui/react";
import Steps from "../components/UI/Steps";
import { Link, Route } from "react-router-dom";
import { updateAInfo } from "../redux/actions/profileActions";
import { USER_AINFO_RESET } from "../redux/constants/profileConstants";
import { getUserDetails } from "../redux/actions/userActions";
import banks from "../components/banks";

const AccountInformation = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  const [accountName, setAccountName] = useState(user && user.accountName);
  const [bvn, setBVN] = useState(user && user.bvn);
  const [nin, setNIN] = useState(user && user.nin);
  const [bank, setBank] = useState(user && user.bank);
  const [accountNumber, setAccountNumber] = useState(
    user && user.accountNumber
  );

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(updateAInfo(bank, accountNumber, accountName, bvn, nin));
  };
  const accountInformation = useSelector((state) => state.accountInformation);
  const { loading, error, success } = accountInformation;

  const toast = useToast();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_AINFO_RESET });
  }

  if (success) {
    toast({
      title: "Success",
      description: "Account Information Updated Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_AINFO_RESET });
    dispatch(getUserDetails());
  }

  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Edit Profile" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.profileGrid}>
            <div className={styles.contentSm}>
              <div className={styles.profilePix}>
                <img
                  src={`/uploads/${user ? user.photo : ""}`}
                  alt={user && user.firstname}
                />
              </div>
            </div>
            <div className={`${styles.contentSm} ${styles.profile}`}>
              <Steps step4 />
              {loading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <form onSubmit={submitHandler}>
                  <div className={styles.formGrid}>
                    <FormControl id="bank" py="2">
                      <FormLabel>Bank</FormLabel>
                      <Select
                        placeholder="Select Bank"
                        value={bank}
                        onChange={(e) => setBank(e.target.value)}
                      >
                        <option>{bank ? bank : "Select ....."}</option>
                        {banks.map((item) => (
                          <option value={item.name} key={item.id}>
                            {item.name}
                          </option>
                        ))}
                      </Select>
                    </FormControl>
                    <FormControl id="accountNumber" py="2">
                      <FormLabel>AccountNumber</FormLabel>
                      <Input
                        type="tel"
                        value={accountNumber}
                        onChange={(e) => setAccountNumber(e.target.value)}
                      />
                    </FormControl>

                    <FormControl id="accountName" py="2">
                      <FormLabel>Account Name</FormLabel>
                      <Input
                        type="text"
                        value={accountName}
                        onChange={(e) => setAccountName(e.target.value)}
                      />
                    </FormControl>

                    <FormControl id="bvn" py="2">
                      <FormLabel>BVN</FormLabel>
                      <Input
                        type="tel"
                        value={bvn}
                        onChange={(e) => setBVN(e.target.value)}
                      />
                    </FormControl>
                    <FormControl id="nin" py="2">
                      <FormLabel>NIN</FormLabel>
                      <Input
                        type="tel"
                        value={nin}
                        onChange={(e) => setNIN(e.target.value)}
                      />
                    </FormControl>
                  </div>
                  <div className={styles.formGrid}>
                    <FormControl py="2">
                      <Button colorScheme="green" type="submit" isFullWidth>
                        Save Information
                      </Button>
                    </FormControl>
                    <FormControl py="2">
                      <Link to="/edit/photo">
                        <Button colorScheme="blue" isFullWidth>
                          Photo Upload
                        </Button>
                      </Link>
                    </FormControl>
                  </div>
                </form>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AccountInformation;
