import React from 'react'
import { Link } from 'react-router-dom'
import styles from './styles.module.css'

const Steps = ({ step1, step2, step3, step4, step5 }) => {
    return (
        <div className={styles.steps}>
            <div className={styles.stepGrid}>
                <div className={`${styles.step} ${step1 && styles.active}`}>
                    <Link to="/edit/profile">Basic Information</Link>
                </div>
                <div className={`${styles.step} ${step2 && styles.active}`}>
                    <Link to="/edit/emergencyContact">Emergency Contact</Link>
                </div>
                <div className={`${styles.step} ${step3 && styles.active}`}>
                    <Link to="/edit/officialInformation">Official Information</Link>
                </div>
                <div className={`${styles.step} ${step4 && styles.active}`}>
                    <Link to="/edit/accountInformation">Account Information</Link>
                </div>
                <div className={`${styles.step} ${step5 && styles.active}`}>
                    <Link to="/edit/photo">Photo Upload</Link>
                </div>
            </div>
        </div>
    )
}

export default Steps
