export const getMyManager = () => async (dispatch, getState) => {
  try {
    dispatch({ type: GET_MANAGER_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.get(
      `/api/v1/staff?select=role&select=firstname,lastname&role=Manager`,
      config
    );

    dispatch({
      type: GET_MANAGER_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_MANAGER_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
