import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  FormControl,
  FormLabel,
  Input,
  Select,
  Textarea,
  Button,
  CircularProgress,
  useToast,
  Center,
} from "@chakra-ui/react";
import states from "../components/states";
import Steps from "../components/UI/Steps";
import { Link, Route } from "react-router-dom";
import { updateEInfo } from "../redux/actions/profileActions";
import { USER_EINFO_RESET } from "../redux/constants/profileConstants";
import { getUserDetails } from "../redux/actions/userActions";

const EmergencyContact = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  const [emergencyContactName, setEmergencyContactName] = useState(
    user && user.emergencyContactName
  );
  const [emergencyContactEmail, setEmergencyContactEmail] = useState(
    user && user.emergencyContactEmail
  );
  const [emergencyContactState, setEmergencyContactState] = useState(
    user && user.emergencyContactState
  );
  const [emergencyContactPhone, setEmergencyContactPhone] = useState(
    user && user.emergencyContactPhone
  );
  const [emergencyContactRelationship, setEmergencyContactRelationship] =
    useState(user && user.emergencyContactRelationship);
  const [emergencyContactAddress, setEmergencyContactAddress] = useState(
    user && user.emergencyContactAddress
  );

  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateEInfo(
        emergencyContactName,
        emergencyContactEmail,
        emergencyContactState,
        emergencyContactPhone,
        emergencyContactAddress,
        emergencyContactRelationship
      )
    );
  };
  const emergencyInformation = useSelector(
    (state) => state.emergencyInformation
  );
  const { loading, error, success } = emergencyInformation;
  const toast = useToast();

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_EINFO_RESET });
  }

  if (success) {
    toast({
      title: "Success",
      description: "Emergency Contact Information Updated Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_EINFO_RESET });
    dispatch(getUserDetails());
  }
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Edit Profile" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.profileGrid}>
            <div className={styles.contentSm}>
              <div className={styles.profilePix}>
                <img
                  src={`/uploads/${user ? user.photo : ""}`}
                  alt={user && user.firstname}
                />
              </div>
            </div>
            <div className={`${styles.contentSm} ${styles.profile}`}>
              <Steps step2 />
              {loading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <form onSubmit={submitHandler}>
                  <div className={styles.formGrid}>
                    <FormControl id="emergencyContactName" py="2">
                      <FormLabel>Emergency Contact Name</FormLabel>
                      <Input
                        type="text"
                        value={emergencyContactName}
                        onChange={(e) =>
                          setEmergencyContactName(e.target.value)
                        }
                      />
                    </FormControl>

                    <FormControl id="emergencyContactEmail" py="2">
                      <FormLabel>Emergency Contact Email</FormLabel>
                      <Input
                        type="email"
                        value={emergencyContactEmail}
                        onChange={(e) =>
                          setEmergencyContactEmail(e.target.value)
                        }
                      />
                    </FormControl>

                    <FormControl id="emergencyContactPhone" py="2">
                      <FormLabel>Emergency Contact Phone</FormLabel>
                      <Input
                        type="tel"
                        value={emergencyContactPhone}
                        onChange={(e) =>
                          setEmergencyContactPhone(e.target.value)
                        }
                      />
                    </FormControl>
                    <FormControl id="emergencyContactState" py="2">
                      <FormLabel>Emergency Contact State</FormLabel>
                      <Select
                        placeholder="Select State"
                        value={emergencyContactState}
                        onChange={(e) =>
                          setEmergencyContactState(e.target.value)
                        }
                      >
                        {states.map((item) => (
                          <option value={item} key={item}>
                            {item}
                          </option>
                        ))}
                      </Select>
                    </FormControl>
                    <FormControl id="emergencyContactRelationship" py="2">
                      <FormLabel>Relationship</FormLabel>
                      <Input
                        type="tel"
                        value={emergencyContactRelationship}
                        onChange={(e) =>
                          setEmergencyContactRelationship(e.target.value)
                        }
                      />
                    </FormControl>
                    <FormControl id="emergencyContactAddress" py="2">
                      <FormLabel>Emergency Contact Address</FormLabel>
                      <Textarea
                        value={emergencyContactAddress}
                        onChange={(e) =>
                          setEmergencyContactAddress(e.target.value)
                        }
                      ></Textarea>
                    </FormControl>
                    <FormControl py="2">
                      <Button colorScheme="green" type="submit" isFullWidth>
                        Save Information
                      </Button>
                    </FormControl>
                    <FormControl py="2">
                      <Link to="/edit/officialInformation">
                        <Button colorScheme="blue" isFullWidth>
                          Official Information
                        </Button>
                      </Link>
                    </FormControl>
                  </div>
                </form>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default EmergencyContact;
