import {
  USER_BASIC_INFO_FAIL,
  USER_BASIC_INFO_REQUEST,
  USER_BASIC_INFO_SUCCESS,
  USER_BASIC_INFO_RESET,
  USER_EINFO_FAIL,
  USER_EINFO_REQUEST,
  USER_EINFO_SUCCESS,
  USER_EINFO_RESET,
  USER_OINFO_FAIL,
  USER_OINFO_REQUEST,
  USER_OINFO_SUCCESS,
  USER_OINFO_RESET,
  USER_PHOTO_FAIL,
  USER_PHOTO_REQUEST,
  USER_PHOTO_SUCCESS,
  USER_PHOTO_RESET,
  USER_AINFO_FAIL,
  USER_AINFO_REQUEST,
  USER_AINFO_SUCCESS,
  USER_AINFO_RESET,
} from "../constants/profileConstants";

export const userBasicInfoReducer = (state = { basicInfo: {} }, action) => {
  switch (action.type) {
    case USER_BASIC_INFO_REQUEST:
      return { ...state, loading: true };
    case USER_BASIC_INFO_SUCCESS:
      return { loading: false, success: true, basicInfo: action.payload };
    case USER_BASIC_INFO_FAIL:
      return { loading: false, error: action.payload };
    case USER_BASIC_INFO_RESET:
      return {};
    default:
      return state;
  }
};

