import React from 'react'
import styles from './styles.module.css'

const AppraisalStep = () => {
    return (
        <div className={styles.stacks}>
            <div className={styles.stepGrid}>
                <div className={styles.stack}>
                    <div className={`${styles.circle} ${styles.green}`}>5</div>
                    <p>Outstanding</p>
                </div>
                <div className={styles.stack}>
                    <div className={`${styles.circle} ${styles.blue}`}>4</div>
                    <p>Superior</p>
                </div>
                <div className={styles.stack}>
                    <div className={`${styles.circle} ${styles.magenta}`}>3</div>
                    <p>Fully Acceptable</p>
                </div>
                <div className={styles.stack}>
                    <div className={`${styles.circle} ${styles.gold}`}>2</div>
                    <p>Conditional</p>
                </div>
                <div className={styles.stack}>
                    <div className={`${styles.circle} ${styles.crimson}`}>1</div>
                    <p>Unsatisfactory</p>
                </div>

            </div>
        </div>
    )
}

export default AppraisalStep
