import {
  GET_SECTIONA_FAIL,
  GET_SECTIONA_REQUEST,
  GET_SECTIONA_SUCCESS,
  ANSWER_SECTIONA_FAIL,
  ANSWER_SECTIONA_REQUEST,
  ANSWER_SECTIONA_SUCCESS,
  ANSWER_SECTIONA_RESET,
  PREVIOUS_SCORE_FAIL,
  PREVIOUS_SCORE_REQUEST,
  PREVIOUS_SCORE_SUCCESS,
  PREVIOUS_SCORE_RESET,
  GET_CURRENTAPPRAISAL_FAIL,
  GET_CURRENTAPPRAISAL_REQUEST,
  GET_CURRENTAPPRAISAL_SUCCESS,
  START_APPRAISAL_FAIL,
  START_APPRAISAL_REQUEST,
  START_APPRAISAL_RESET,
  START_APPRAISAL_SUCCESS,
  GET_ALL_FAIL,
  GET_ALL_REQUEST,
  GET_ALL_SUCCESS,
  UPDATE_APPRAISAL_FAIL,
  UPDATE_APPRAISAL_REQUEST,
  UPDATE_APPRAISAL_RESET,
  UPDATE_APPRAISAL_SUCCESS,
  GET_INIT_FAIL,
  GET_INIT_REQUEST,
  GET_INIT_SUCCESS,
  ADD_INIT_FAIL,
  ADD_INIT_REQUEST,
  ADD_INIT_SUCCESS,
  ADD_INIT_RESET,
  GET_SCORE_FAIL,
  GET_SCORE_REQUEST,
  GET_SCORE_SUCCESS,
  UPLOAD_SCORE_FAIL,
  UPLOAD_SCORE_REQUEST,
  UPLOAD_SCORE_SUCCESS,
  UPLOAD_SCORE_RESET,
} from "../constants/appraisalConstants";

export const sectionAReducer = (state = { sectionA: [] }, action) => {
  switch (action.type) {
    case GET_SECTIONA_REQUEST:
      return { ...state, loading: true };
    case GET_SECTIONA_SUCCESS:
      return { loading: false, success: true, sectionA: action.payload.data };
    case GET_SECTIONA_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

export const sectionAAnswerReducer = (state = {}, action) => {
  switch (action.type) {
    case ANSWER_SECTIONA_REQUEST:
      return { state, loading: true };
    case ANSWER_SECTIONA_SUCCESS:
      return { loading: false, success: true };
    case ANSWER_SECTIONA_FAIL:
      return { loading: false, error: action.payload };
    case ANSWER_SECTIONA_RESET:
      return {};
    default:
      return state;
  }
};

