import React, { useState } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  FormControl,
  FormLabel,
  Input,
  CircularProgress,
  useToast,
  Center,
} from "@chakra-ui/react";
import Steps from "../components/UI/Steps";
import { getUserDetails } from "../redux/actions/userActions";
import axios from "axios";
import { Route } from "react-router-dom";

const PhotoUpload = () => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;

  const [uploading, setUploading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);
  const [errorInfo, setErrorInfo] = useState(false);

  const dispatch = useDispatch();

  const uploadFileHandler = async (e) => {
    const file = e.target.files[0];
    const formData = new FormData();
    formData.append("file", file);
    setUploading(true);

    const userInfo = JSON.parse(localStorage.getItem("userInfo"));
    try {
      const config = {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `Bearer ${userInfo.token}`,
        },
      };

      await axios.post("/api/v1/staff/uploadPix", formData, config);

      setSuccess(true);
      setUploading(false);
    } catch (error) {
      setError(true);
      setErrorInfo(error.response.data.error);
      setUploading(false);
    }
  };
  const toast = useToast();

  if (error) {
    toast({
      title: "Error",
      description: errorInfo,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    setError(false);
  }

  if (success) {
    toast({
      title: "Success",
      description: "Photo Uploaded Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    setSuccess(false);
    dispatch(getUserDetails());
  }

  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Edit Profile" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.profileGrid}>
            <div className={styles.contentSm}>
              <div className={styles.profilePix}>
                <img
                  src={`/uploads/${user ? user.photo : ""}`}
                  alt={user && user.firstname}
                />
              </div>
            </div>
            <div className={`${styles.contentSm} ${styles.profile}`}>
              <Steps step5 />
              {uploading ? (
                <Center>
                  <CircularProgress isIndeterminate color="green.300" />
                </Center>
              ) : (
                <div className={styles.formGrid}>
                  <FormControl id="photo" py="2">
                    <FormLabel>Photo</FormLabel>
                    <Input type="file" onChange={uploadFileHandler} />
                  </FormControl>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PhotoUpload;
