import { Button } from "@chakra-ui/button";
import React, { useState } from "react";
import styles from "./styles.module.css";
import { useSelector, useDispatch } from "react-redux";
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Textarea,
  useToast,
  Center,
  CircularProgress,
} from "@chakra-ui/react";
import { addSuggestion } from "../../redux/actions/feedbackActions";
import { ADD_SUGGESTION_RESET } from "../../redux/constants/feedbackConstants";

const Header = ({ title }) => {
  const userDetails = useSelector((state) => state.userDetails);
  const { user } = userDetails;
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();
  const toast = useToast();

  const suggestion = useSelector((state) => state.suggestion);
  const { loading, error, success } = suggestion;

  const [message, setMessage] = useState("");
  const dispatch = useDispatch();

  const submitHandler = () => {
    dispatch(addSuggestion(message));
  };

  if (success) {
    onClose();
    toast({
      title: "Success",
      description: "Suggestion has been sent",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_SUGGESTION_RESET });
  }

  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_SUGGESTION_RESET });
  }

  return (
    <div className={styles.header}>
      <div className={styles.headerGrid}>
        <div className={`${styles.card}`}>
          <p className={styles.pageTitle}>{title}</p>
        </div>
        <div className={`${styles.card}`}>
          <div className={styles.dp}>
            <img
              src={`/uploads/${user ? user.photo : ""}`}
              alt={user && user.firstname}
            />
          </div>
          <small>Hello {user && user.firstname}!</small>
          <Button
            colorScheme="orange"
            size="sm"
            ref={btnRef}
            onClick={onOpen}
            className={styles.btn}
          >
            Do you have any Suggesstions?
          </Button>
          &nbsp;&nbsp;
          <Button colorScheme="red" size="sm" className={styles.btn}>
            Make a Complaint
          </Button>
        </div>
      </div>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        finalFocusRef={btnRef}
      >
        <DrawerOverlay />
        <DrawerContent>
          <DrawerCloseButton />
          <DrawerHeader>Make a Suggesstion</DrawerHeader>

          <DrawerBody>
            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
              </Center>
            ) : (
              <Textarea
                placeholder="Type here..."
                onChange={(e) => setMessage(e.target.value)}
                value={message}
              />
            )}
          </DrawerBody>

          <DrawerFooter>
            <Button variant="outline" mr={3} onClick={onClose}>
              Cancel
            </Button>
            <Button colorScheme="blue" onClick={submitHandler}>
              Save
            </Button>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
    </div>
  );
};

export default Header;
