import { createStore, combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";
import {
  getDeptReducer,
  getManagerReducer,
  userDetailsReducer,
  userLoginReducer,
} from "./reducers/userReducers";
import {
  userAccountInfoReducer,
  userBasicInfoReducer,
  userEmergencyInfoReducer,
  userOfficeInfoReducer,
  userPhotoReducer,
} from "./reducers/profileReducers";
import {
  addInitReducer,
  AllAppraisalReducer,
  currentAppraisalReducer,
  getInitReducer,
  getScoreReducer,
  previousScoreReducer,
  sectionAAnswerReducer,
  sectionAReducer,
  startAppraisalReducer,
  updateAppraisalReducer,
  uploadScoreReducer,
} from "./reducers/appraisalReducers";
import { getAllStaffReducer } from "./reducers/adminReducers";
import { sectionBReducer } from "./reducers/initReducers";
import { getTeamReducer } from "./reducers/teamReducers";
import {
  complaintReducer,
  suggestionReducer,
} from "./reducers/feedbackReducers";

const reducer = combineReducers({
  userLogin: userLoginReducer,
  userDetails: userDetailsReducer,
  basicInformation: userBasicInfoReducer,
  emergencyInformation: userEmergencyInfoReducer,
  officialInformation: userOfficeInfoReducer,
  accountInformation: userAccountInfoReducer,
  userPhoto: userPhotoReducer,
  departments: getDeptReducer,
  getSectionA: sectionAReducer,
  getSectionB: sectionBReducer,
  responseA: sectionAAnswerReducer,
  getPreviousScore: previousScoreReducer,
  start: startAppraisalReducer,
  current: currentAppraisalReducer,
  all: AllAppraisalReducer,
  update: updateAppraisalReducer,
  getInit: getInitReducer,
  addInit: addInitReducer,
  staff: getAllStaffReducer,
  myScore: getScoreReducer,
  uploadmyScore: uploadScoreReducer,
  myTeam: getTeamReducer,
  myManager: getManagerReducer,
  suggestion: suggestionReducer,
  complaint: complaintReducer,
});

const userInfoFromStorage = localStorage.getItem("userInfo")
  ? JSON.parse(localStorage.getItem("userInfo"))
  : null;
const userDetailsFromStorage = localStorage.getItem("user")
  ? JSON.parse(localStorage.getItem("user"))
  : null;

const initialState = {
  userLogin: { userInfo: userInfoFromStorage },
  userDetails: { user: userDetailsFromStorage },
  //passwordUpdate: { userInfo: userInfoFromStorage },
};

const middleware = [thunk];

const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
