import axios from "axios";
import {
  ADD_SUGGESTION_REQUEST,
  ADD_SUGGESTION_FAIL,
  ADD_SUGGESTION_SUCCESS,
  ADD_COMPLAINT_SUCCESS,
  ADD_COMPLAINT_REQUEST,
  ADD_COMPLAINT_FAIL,
} from "../constants/feedbackConstants";

export const addComplaint = (id) => async (dispatch, getState) => {
  try {
    dispatch({ type: ADD_COMPLAINT_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.post(`/api/v1/staff/forms/complaint`, config);

    dispatch({
      type: ADD_COMPLAINT_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: ADD_COMPLAINT_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};

export const addSuggestion = (message) => async (dispatch, getState) => {
  try {
    dispatch({ type: ADD_SUGGESTION_REQUEST });

    const {
      userLogin: { userInfo },
    } = getState();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${userInfo.token}`,
      },
    };
    const { data } = await axios.post(
      `/api/v1/staff/forms/suggestion`,
      { message },
      config
    );

    dispatch({
      type: ADD_SUGGESTION_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: ADD_SUGGESTION_FAIL,
      payload:
        error.response && error.response.data.error
          ? error.response.data.error
          : error.message,
    });
  }
};
