import React, { useState, useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import { Link, Route } from "react-router-dom";
import {
  Button,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
} from "@chakra-ui/react";
import axios from "axios";

const Attendance = () => {
  const [logs, setLogs] = useState([]);
  useEffect(() => {
    axios
      .get("https://vmsapi.lotusbetaanalytics.com/api/Attendance")
      .then((response) => {
        setLogs(response.data.items);
        console.log(response.data);
      });
  }, []);
  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Attendance" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            <Table variant="striped" colorScheme="teal">
              <TableCaption>Lban Attendance</TableCaption>
              <Thead>
                <Tr>
                  <Th>Name</Th>
                  <Th>Latitude</Th>
                  <Th>Longitude</Th>
                  <Th>Time-in</Th>
                  <Th>Time-Out</Th>
                </Tr>
              </Thead>
              <Tbody>
                {logs &&
                  logs.map((item, index) => (
                    <Tr key={index}>
                      <Td>{item.firstName + " " + item.lastName}</Td>
                      <Td>{item.latitude}</Td>
                      <Td>{item.longitude}</Td>
                      <Td>{item.timeIn}</Td>
                      <Td>{item.timeOut}</Td>
                      <Td></Td>
                    </Tr>
                  ))}
              </Tbody>
            </Table>

            {/* <Link to="/appraisal/sectionA">
              <Button colorScheme="teal">Start Appraisal</Button>
            </Link> */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Attendance;
