import React from 'react'
import ReactECharts from "echarts-for-react";

const Chart = ({ report }) => {
    const option = {
        tooltip: {
            trigger: "item",
        },

        legend: {
            top: "5%",
            left: "center",
        },
        series: [
            {
                name: "KPI",
                type: "pie",
                radius: ["40%", "70%"],
                avoidLabelOverlap: false,
                itemStyle: {
                    borderRadius: 10,
                    borderColor: "#fff",
                    borderWidth: 2,

                },
                label: {
                    show: false,
                    position: "center",
                },
                emphasis: {

                    label: {
                        show: true,
                        fontSize: "20",
                        fontWeight: "bold",
                    },
                },
                labelLine: {
                    show: false,
                },
                data: [
                    { value: 50, name: "First Quarter" },
                    { value: 45, name: "Second Quarter" },
                    { value: 40, name: "Third Quarter" },
                    { value: 30, name: "Four Quarter" },
                    { value: 45, name: "Overall" },

                ],
            },
        ],
    };
    return (
        <div>
            <ReactECharts option={option} style={{ height: '500px', width: '100%' }} />
        </div>
    )
}

export default Chart
