import React, { useState, useEffect } from "react";
import Navigation from "../components/Navigation";
import Header from "../components/UI/Header";
import styles from "./styles.module.css";
import {
  FormControl,
  FormLabel,
  Input,
  Select,
  Button,
  CircularProgress,
  useToast,
  Center,
  Textarea,
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerContent,
  DrawerCloseButton,
  useDisclosure,
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
  Alert,
  AlertIcon,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import {
  addInitiative,
  getInitiative,
} from "../redux/actions/appraisalActions";
import { ADD_INIT_RESET } from "../redux/constants/appraisalConstants";
import { getAllStaffs } from "../redux/actions/adminActions";
import { Route } from "react-router-dom";

const SetInit = () => {
  const [perspective, setPerspective] = useState("");
  const [objective, setObjective] = useState("");
  const [measures, setMeasures] = useState("");
  const [target, setTarget] = useState("");
  const [initiative, setInitiative] = useState("");
  const [user, setUser] = useState("");
  const toast = useToast();

  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = React.useRef();

  const dispatch = useDispatch();

  //   get initiatives
  const getInit = useSelector((state) => state.getInit);
  const { loading, error, init } = getInit;

  //   Add Initiatives
  const submitHandler = () => {
    const session = new Date().getFullYear();
    dispatch(
      addInitiative(
        perspective,
        objective,
        measures,
        target,
        initiative,
        session,
        user
      )
    );
  };
  const staff = useSelector((state) => state.staff);
  const { staffs } = staff;
  const addInit = useSelector((state) => state.addInit);
  const { loading: adding, error: errorAdd, success } = addInit;
  if (success) {
    onClose();
    toast({
      title: "Success",
      description: "Initiative Added Successfully",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_INIT_RESET });
    dispatch(getInitiative());
  }
  if (errorAdd) {
    toast({
      title: "Error",
      description: errorAdd,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: ADD_INIT_RESET });
  }

  //   Delete Initiatives
  const deleteHandler = () => {
    dispatch();
  };

  useEffect(() => {
    dispatch(getInitiative());
    dispatch(getAllStaffs());
  }, [dispatch]);

  return (
    <div>
      <Route render={({ history }) => <Navigation history={history} />} />
      <div className={styles.mainContents}>
        <div className={styles.overlay}>
          <Header title="Set Initiatives" />
        </div>
        <div className={styles.contentSm}>
          <div className={styles.appraisal}>
            <h3>Balanced Scorecard</h3>
            <Button ref={btnRef} colorScheme="teal" onClick={onOpen}>
              Set Initiative
            </Button>
            <Drawer
              isOpen={isOpen}
              placement="right"
              onClose={onClose}
              finalFocusRef={btnRef}
              size="md"
            >
              <DrawerOverlay />
              <DrawerContent>
                <DrawerCloseButton />
                <DrawerHeader>Set Initiatives</DrawerHeader>

                <DrawerBody>
                  {adding ? (
                    <Center>
                      <CircularProgress isIndeterminate color="green.300" />
                    </Center>
                  ) : (
                    <>
                      <FormControl id="bank" py="2">
                        <FormLabel>User</FormLabel>
                        <Select
                          placeholder="Select User"
                          value={user}
                          onChange={(e) => setUser(e.target.value)}
                        >
                          {staffs &&
                            staffs.map((item) => (
                              <option value={item._id} key={item._id}>
                                {item.email}
                              </option>
                            ))}
                        </Select>
                      </FormControl>
                      <FormControl id="perspective" py="2">
                        <FormLabel>Perspective</FormLabel>
                        <Input
                          type="text"
                          value={perspective}
                          onChange={(e) => setPerspective(e.target.value)}
                        />
                      </FormControl>
                      <FormControl id="objective" py="2">
                        <FormLabel>Objective</FormLabel>
                        <Input
                          type="text"
                          value={objective}
                          onChange={(e) => setObjective(e.target.value)}
                        />
                      </FormControl>
                      <FormControl id="measures" py="2">
                        <FormLabel>Measures</FormLabel>
                        <Select
                          placeholder="Select measures"
                          value={measures}
                          onChange={(e) => setMeasures(e.target.value)}
                        >
                          <option value="Annual">Annual</option>
                          <option value="Bi Annual">Bi Annual</option>
                          <option value="Quarterly">Quarterly</option>
                          <option value="Monthly">Monthly</option>
                          <option value="Monthly/Quarterly">
                            Monthly/Quarterly
                          </option>
                        </Select>
                      </FormControl>
                      <FormControl id="target" py="2">
                        <FormLabel>Target</FormLabel>
                        <Textarea
                          type="text"
                          value={target}
                          onChange={(e) => setTarget(e.target.value)}
                        ></Textarea>
                      </FormControl>
                      <FormControl id="initiative" py="2">
                        <FormLabel>Initiative</FormLabel>
                        <Textarea
                          type="text"
                          value={initiative}
                          onChange={(e) => setInitiative(e.target.value)}
                        ></Textarea>
                      </FormControl>
                    </>
                  )}
                </DrawerBody>

                <DrawerFooter>
                  <Button variant="outline" mr={3} onClick={onClose}>
                    Cancel
                  </Button>
                  <Button colorScheme="blue" onClick={submitHandler}>
                    Save
                  </Button>
                </DrawerFooter>
              </DrawerContent>
            </Drawer>

            {error && (
              <Alert status="warning">
                <AlertIcon />
                {error}
              </Alert>
            )}
            {loading ? (
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
              </Center>
            ) : (
              <Table variant="striped" colorScheme="teal">
                <TableCaption>Lban Appraisal</TableCaption>
                <Thead>
                  <Tr>
                    <Th>Perspectives</Th>
                    <Th>Objectives</Th>
                    <Th>Measures</Th>
                    <Th>Targets</Th>
                    <Th>Initiative</Th>
                    <Th>Actions</Th>
                  </Tr>
                </Thead>
                <Tbody>
                  {init &&
                    init.map((item) => (
                      <Tr key={item._id}>
                        <Td>{item.perspective}</Td>
                        <Td>{item.objective}</Td>
                        <Td>{item.measures}</Td>
                        <Td>{item.target}</Td>
                        <Td>{item.initiative}</Td>
                        <Td>
                          <Button
                            type="button"
                            colorScheme="red"
                            onClick={() => deleteHandler(item._id)}
                            size="xs"
                          >
                            Delete
                          </Button>
                        </Td>
                      </Tr>
                    ))}
                </Tbody>
              </Table>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SetInit;
