import {
  ADD_COMPLAINT_FAIL,
  ADD_COMPLAINT_REQUEST,
  ADD_COMPLAINT_SUCCESS,
  ADD_SUGGESTION_FAIL,
  ADD_SUGGESTION_REQUEST,
  ADD_SUGGESTION_SUCCESS,
  ADD_SUGGESTION_RESET,
  ADD_COMPLAINT_RESET,
} from "../constants/feedbackConstants";

export const complaintReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_COMPLAINT_REQUEST:
      return { state, loading: true };
    case ADD_COMPLAINT_SUCCESS:
      return { loading: false, success: true };
    case ADD_COMPLAINT_FAIL:
      return { loading: false, error: action.payload };
    case ADD_COMPLAINT_RESET:
      return {};
    default:
      return state;
  }
};

export const suggestionReducer = (state = {}, action) => {
  switch (action.type) {
    case ADD_SUGGESTION_REQUEST:
      return { state, loading: true };
    case ADD_SUGGESTION_SUCCESS:
      return { loading: false, success: true };
    case ADD_SUGGESTION_FAIL:
      return { loading: false, error: action.payload };
    case ADD_SUGGESTION_RESET:
      return {};
    default:
      return state;
  }
};
